<template>
  <div class="footer">
   
    <ul>
      <li v-for="(data,index) in footerData">
        <router-link :to="data.path">{{data.title}}</router-link>
      </li>
    </ul>
  </div>
  
</template>

<script>

export default {
  name: 'zs-footer',
  data () {
    return {
      footerData:[
        {path:"/",title:"首页"},
        {path:"/cart",title:"购物车"},
        {path:"/list",title:"列表"}
      ]
    }
  },
  mounted () {
    console.log(this)
  }
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

.footer { position: fixed; bottom:0 ; left: 0; width: 100%; line-height: 50px; border-top: 1px solid #ccc;background:#fff; text-align: center;z-index:10}
 
  .footer ul {  overflow:hidden}
  .footer li {float:left; width:33%}
  /*不精准的当前路径(当前的路径包含它) */
  .router-link-active {}
   /*精准的当前的路径 */
  .router-link-exact-active { color:red}
 
</style>



