<template>
  <div class="header">
    <ul>
      <li class="header-btn"> 
        <slot name="left"></slot>
      </li>
      <li class="header-tit">{{title}}</li>
      <li class="header-btn"> 
        <slot name="right"></slot>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'v-header',
  props:{
    title:{
      type:String,
      default:"网站的头部"
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

.header { position: fixed; top:0 ; left: 0; width: 100%; line-height: 50px; border-bottom: 1px solid #ccc; text-align: center; background:#fff; z-index:10}
 
  .header ul { display: flex; }
  .header-btn { width:60px;}
  .header-tit { flex: 1}
</style>



