import Vue from 'vue'
import Router from 'vue-router'
// 导入页面
import Index from '@/modules/Index'
import Cart from '@/modules/Cart'
import Detail from '@/modules/Detail'
import List from '@/modules/List'
import Mine from '@/modules/Mine'
import Password from '@/modules/Mine/Password'
import MineList from '@/modules/Mine/Minelist'
import History from '@/modules/Mine/History'
import Order from '@/modules/Mine/Order'


Vue.use(Router) //使用路由插件

const router =  new Router({

  // 配置路由
  routes: [

    {
      path: '/',
      name: 'Index',
      component: Index
    },
     {
      path: '/cart',
      name: 'Cart',
      meta:{requiresAuth:true}, //当前页面需要登录，才能进入
      component: Cart
    },
     {
      path: '/list',
      name: 'List',
      component: List

    },
    {
      path: '/login',
      name: 'Login',
      component: {template:'<div>login</div>'}
    },
    {
      // 配置详情页面的路由，以及接收的参数id
      path: '/detail/:id',
      name: 'Detail',
      component: Detail
    },
    {
      path: '/mine',
      name: 'Mine',
      meta:{requiresAuth:true},
      component: Mine,
      children:[
        {path:"",component:MineList},
        {path:"password",component:Password},
        {path:"order",component:Order},
        {path:"history",component:History}
      ]
    },
    //路由重定向
    // {
    //   path:"*",redirect:"/"
    // }

  ]
});






//登录验证
const auth = {
  loggedIn(){
      var userInfo =JSON.parse( window.localStorage.getItem("userInfo")||'{}')
      return userInfo.uid
  }
};

//循环所有的页面的 beforRouterEnter，判断哪个页面需要登录才能进入
router.beforeEach((to, from, next) => {

  if (to.matched.some(record => record.meta.requiresAuth)) {//判断当前页面是否需要登录

    if (!auth.loggedIn()) {//判断是否登录

      next({ //没有登录，就去登录页面
        path: '/login',
        query: { redirect: to.fullPath } //参数是，想要去的页面
      })
    } else {
      //登录了，就执行下一步
      next()
    }
  } else {
    next() // 当前页面不需要登录验证，就直接进入
  }
});

export default router


