import Vue from 'vue'
import { Indicator } from 'mint-ui';

export  default  {
  state:{
    cartData:[],
    cont:20,
    allSelected:false
  },
  //给组件提供的数据
  getters:{
    totalInfo(state){
      var price = 0;
      var num = 0;
      state.cartData.forEach(ele=>{
        //只计算被选中的商品
        if(ele.selected){
          price+=ele.price*ele.number
          num+=ele.number*1
        }

      });
      return {price,num}
    },
    cartData:(state)=>state.cartData,
    //通过getters快捷获取
    allSelected:(state)=>state.allSelected

  },
  mutations:{
    setCartData(state,payload){
      state.cartData = payload.cartData
    },
    changeCartData(state,{type,index}){
      //让数量+type
      state.cartData[index].number =state.cartData[index].number*1+ type
    },
    //改变选中的状态
    changeSelected(state,{index}){
      if(index!=-1){
        //li被点击
        //让选中的状态取反
        state.cartData[index].selected = !state.cartData[index].selected;
        var all = true; //假设全部选中了
        state.cartData.forEach(ele=>{
          if(!ele.selected){
            //如果有一个没有被选择
            all = false
          }
        });
        state.allSelected = all
      }else {
        //全选被点击
        state.allSelected = !state.allSelected
        //让所有li的选中状态和自己一样
        state.cartData.forEach(ele=>{
          //让每一个li的状态都  = 全选的状态
          ele.selected = state.allSelected
        })
      }

    }
  },
  //给组件提供的方法
  actions:{
    getCartData(context){
      //context =>简单版store
      //发起数据请求
      Vue.http.jsonp("http://datainfo.duapp.com/shopdata/getCar.php?userID=tom666")
        .then(res=>{
          console.log(res.data);//[{},{}]
          //把数据提交给管理员
          //给每一条购物车信息都添加一个selected属性表示被选中
          res.data.forEach(ele=>{
            ele.selected= false
          });

          console.log(res.data);
          context.commit("setCartData",{cartData:res.data})
        })
    },
    changeCartDataAction(context,{type,index}){
      const url = "http://datainfo.duapp.com/shopdata/updatecar.php";
      //context.state.cartData =>获取购物车数据  =》id, number
      const id = context.state.cartData[index].goodsID; //商品id
      let number = context.state.cartData[index].number*1; //商品数量
      number+=type; //改变数量
      const params = `?userID=tom666&goodsID=${id}&number=${number}`;

      Indicator.open('Loading...'); //数据请开始，显示loading
      Vue.http.get(url+params).then(res=>{


        Indicator.close();//数据请结束，关闭loading
        if(res.data){
          //请求成功 ,提交个管理员，修改数据，组件自动更新
          context.commit("changeCartData",{type,index})
        }
      })

    }
  }

}



