<template>
  <ul class="cart-list">
    <li class="cart-item" v-for="(data,index) in cartData" :key="index">
      <span :class="data.selected?'item-ch selected':'item-ch'" @click="changeSelected(index)"></span>
      <img :src="data.goodsListImg" alt="">
      <div class="info">
        <p>{{data.goodsName}}</p>
        <p>{{data.price}}</p>
        <p>{{data.number}}</p>
        <button @click="changeCartDataAction({type:1,index})">+</button>
        <button @click="changeCartDataAction({type:-1,index})">-</button>
      </div>
    </li>

  </ul>
</template>

<script>

import {mapGetters,mapActions} from  "vuex"
export default {
  name: 'cart-list',
  computed:{
      //传递数据到组件
    ...mapGetters(["cartData"])
  },
  methods:{
      //传递action的方法到组件
    ...mapActions(["changeCartDataAction"]),
    changeSelected(index){
        //控制 商品是否选择
      this.$store.commit("changeSelected",{index})
    }
  }
}


</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style  lang="scss">
  .cart-list { background-color: #eee}
  .item-ch { width: 26px; display: inline-block; margin-right:10px; height: 26px; background-color: #ccc; border-radius: 50%;
    position: relative}
  .item-ch:after{content: '';background-color: #eee; width: 20px; height: 20px; position: absolute; left: 3px; top: 3px; border-radius: 50%;}

  .selected:after { background-color: #f71}
  .cart-item { margin-bottom: 10px; background-color: #fff; overflow: hidden;
    padding: 10px; display:flex;

    img {width: 100px; height: 100px;}
    .info { flex: 1; padding-left: 10px;}
    button { padding:5px 10px;}
  }

</style>



