import Vue from 'vue'
import { Indicator } from 'mint-ui';
export  default  {
  state:{
    cont:10,
    detailData:{}, //详情数据
  },
  mutations:{
    increment(state){
      state.cont++
    },
    setDetailData(state,payload){
      //接收  详情数据，改变state
      console.log("mutations payload",payload)
      state.detailData = payload
    },
  },
  actions: {
    getDetailData(context,{id}){
      console.log(context)
      //const {id} = payload
      //获取详情数据(在组件外部使用vue-resource  通过  Vue.http)
      Vue.prototype.axios.get("/api/item/desc?id="+id).then(res=>{
        console.log(res.data)
        //获取到数据以后，需要提交给mutations
        context.commit("setDetailData",res.data.data)
      })
    }
  }
}



