import Vuex from 'vuex'
import Vue from 'vue'
import { Indicator } from 'mint-ui';
import cart from "./modules/cart"
import detail from "./modules/detail"

//使用vuex插件
Vue.use(Vuex);


const store = new Vuex.Store({
  modules:{
    detail,
    cart
  }
});


export default store




