<template>
    <ul class="list"
    v-infinite-scroll="loadMore"
    infinite-scroll-disabled="loading"
    infinite-scroll-distance="10"
    >
        <li v-for="(data,index) in listData">
            <router-link :to="'/detail/'+data.id"> <img :src="data.skuList[0].image" alt=""></router-link>
            <div class="item-info">
                <p>{{data.masterName}}</p>
                <p >
                <span class="price"> {{data.slaveName}}</span>
                </p>
            </div>

        </li>
    </ul>

</template>

<script>
export default {
  data(){
    return {
        loading:false
    }
  },
  methods:{
    loadMore(){
        this.loading = true
        console.log("加载更多")
        //让父组件加载更多
        this.$emit("loadmore",()=>{
            //传参，  ： 回调函数，数据请求完成，结束loading的状态
            this.loading=false
        })
    }
  },
  props:{
      listData:{
          type:Array,
          default(){
              return []
          }
      }
  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.list { background:#eee }
.list li  { padding:10px; margin:10px 0; background:#fff; position:relative}
.list img { width:100px; position:absolute; left:10px; top:10px;}
.item-info {  height:130px; padding-left:110px; }
 .price { color:#f93}
</style>



