<template>
  <div class="page">
  <!-- 使用组件 -->
    <v-header title="首页">
      <span class="menu" slot="left"> </span>
      <span slot="right">消息</span>
    </v-header>
    <v-footer></v-footer>
    <v-content>
      <zs-index-list @loadmore="loadMore"  :list-data="listData"></zs-index-list>
    </v-content>

  </div>
</template>

<script>
//导入子组件
import IndexList from './index-list'
import qs from "qs"

export default {
  name: 'header',
  data(){
    return {
      page:1,
      listData:[]
    }
  },
  methods:{
    loadMore(callback){
      this.page++;
      this.$http.get("/api/recommend/home?page="+this.page+"&num=20").then(res=>{
        //加载更多获取的数据，应该和之前的数据拼接在一起
        this.listData =this.listData.concat(res.data.data.list)
        //调用回调函数
        callback()
      })
    }
  },

  mounted(){
    //console.log(this.axios)
    this.Toast("哈哈哈")
    //获取数据
    this.$http.get("/api/recommend/home?page=1&num=20").then(res=>{
      this.listData =res.data.data.list
    });
      //this.$route.params
    console.log(this.$store) //访问 vuex 的仓库

    //$http 参数 = {} //  "{}"不能被解析
    var params =JSON.stringify( {"platform":1,"uid":"Rwypcu9PenizM3B72t2rqQLp1yC6iojC","lang":1,"traceId":11984,"pageNo":1,"pageSize":50});

    //axios 不支持jsonp
    var parms2 = qs.stringify({
      data:params
    });

    this.$http.get("/ma/api/1007/1?"+parms2).then(res=>{
        console.log(res.data)
    })


  },
  components:{
    "zs-index-list":IndexList
  }
}

var obj = {a:1,b:2}

console.log("json字符串：",JSON.stringify(obj)); //{"a":1,"b":2} 转化成json字符串
console.log("qs字符串：",qs.stringify(obj)) //a=1&b=2  转化成search参数的格式
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .menu { height:1px; background:#000; display:inline-block; width:30px; position:relative; transition:.7s}
  .menu:hover { transform:rotate(90deg)}
  .menu:after {content:""; height:1px; background:#000; display:inline-block; width:30px; position:absolute; top: -10px; left:0}
  .menu:before {content:""; height:1px; background:#000; display:inline-block; width:30px; position:absolute;bottom: -10px;left:0}
</style>



