<template>
  <div class="page">
  <!-- 使用组件 -->
    <v-header title="购物车">
        <a href="javascript:;" slot="right">结算</a>
    </v-header>
    <v-footer></v-footer>
    <div class="sub-header">
      <span :class="allSelected?'item-ch selected':'item-ch'" @click="changeSelected(-1)"></span>
      <span>总金额：<em>￥{{totalInfo.price}}</em></span>
      <span>总数量：<em>{{totalInfo.num}}</em></span>
    </div>
    <v-content  >
        <v-cart-list ></v-cart-list>
    </v-content>
  </div>
</template>

<script>
// 私有的组件 局部注册使用
import CartList from './cart-list'
import {mapGetters,mapActions} from "vuex"

export default {
  computed:{
      //传递数据到组件
    ...mapGetters(["totalInfo","allSelected"])
  },
  components:{
     "v-cart-list": CartList
  },
  methods:{
      //传递方法到组件
    ...mapActions(["getCartData"]),
    changeSelected(index){
      //控制 商品是否选择
      this.$store.commit("changeSelected",{index})
    }
  },
  mounted(){
    //调用方法获取数据
    this.getCartData()
   }
// ,
//  beforeRouteEnter (to, from, next) {
//    // 在渲染该组件的对应路由被 confirm 前调用
//    // 不！能！获取组件实例 `this`
//    // 因为当钩子执行前，组件实例还没被创建
//
//    console.log(to) //去哪
//    console.log(from) //从哪里
//    console.log(next) //走（去）
//    var userInfo =window.localStorage.getItem("userInfo");
//    if(userInfo){//判断有么有保存过用户信息，有说明就登录了
//      next() //执行下一步
//    }else {
//        //去登录
//      next({path: '/login'}) //下一步，改路径 （去登录）
//    }
//  }
}



</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .sub-header  {position: fixed; top: 50px; left: 0; width: 100%; line-height: 36px; padding: 0 10px; background-color: #fff; border-bottom: 1px solid #ccc}
  .sub-header~ .content { padding-top: 86px !important;}
  .swiper-wrap img { width:100%}

  .swiper-wrap { height:200px;}
</style>



