<template>
  <div class="page">
  <!-- 使用组件 -->
    <v-header title="详情">
      <span class="menu" slot="left"> </span>
      <span slot="right" @click="sheetVisible=true">分享</span>
    </v-header>

    <v-content>
        <p class="text">{{cont}}</p>
        <button  class="btn" @click="fnIncrement">increment</button>
         <p class="text">
            <span class="iconfont icon-faxian"></span>
            <span class="iconfont icon-cart"></span><span class="iconfont">&#xe623;</span>
         </p>
        <div class="detail-desc" v-html="detailData.desc">

        </div>
    </v-content>

    <mt-actionsheet
    :actions="actions"
    v-model="sheetVisible">
    </mt-actionsheet>
  </div>
</template>

<script>



export default {
    data(){
        return {
            actions:[
                {
                    name:"分享到qq",
                    method(){
                        alert("qq")
                    }
                },
                {
                    name:"分享到朋友圈",
                    method(){
                        alert("朋友圈")
                    }
                }
            ],
            sheetVisible:false
        }
    },
    name:"detail",

    computed:{
        cont(){
            // 通过计算属性获取 vuex  store里面的数据
            return this.$store.state.cont
        },
        detailData(){
            //获取详情数据
            return this.$store.state.detail.detailData
        }
    },
    methods:{
        fnIncrement(){
            //让cont++  通过commit 提交请求， 修改数据
            this.$store.commit("increment")
        }
    },
    mounted(){
        this.$http.get("/static/mock/detail.json").then(res=>{
           console.log( res.data)
        })

        //获取id
        console.log(this)
        console.log(this.$route.params.id)//接收路由传参
        var id = this.$route.params.id;


        this.$store.dispatch("getDetailData",{id})
    }
}


// 组件需要详情数据 detailData（异步的数据操作）
//1、在 store里面 添加  detailData:{}
//2、在组件内部通过computed获取数据
//3、当组件mounted的时候， 发起一个 请求 （action）  ，获取商品的真实数据
//4、store里面的actions接收到请，get获取服务器数据,获取到数据以后，提交给mutations
//5、mutation接收提交的数据，改变store的state，组件会自动更新 =》detailData

</script>

<!-- Add "scoped" attribute to limit CSS to this component only


    lang="scss" 引入的外部的css 不会受scoped局部样式的影响
    lang="css" 引入的外部的scss 不会受scoped局部样式的影响
-->
<style   lang="scss">

    @import "./detail.scss";

    %box { padding:10px 20px;}
    $color1:green;
    .page {
        .text { color: $color1; @extend %box}
        .btn { color:blue; @extend %box}
    }
</style>



