<template>
    <div>
    <v-header></v-header>
    <v-content>
        <mt-loadmore
        :top-method="loadTop"
        :bottom-method="loadBottom"
        :autoFill="false"
        ref="loadmore">
            <ul class="list">
                <li v-for="(data,index) in listData">
                    <router-link :to="'/detail/'+data.id">
                        <img  v-lazy="data.skuList[0].image" alt="">
                    </router-link>
                    <div class="item-info">
                        <p>{{data.masterName}}</p>
                        <p >
                        <span class="price"> {{data.slaveName}}</span>
                        </p>
                    </div>

                </li>
            </ul>
        </mt-loadmore>
    </v-content>
    </div>
</template>

<script>
    export default {
        name:"list",
        data(){
            return {
                page:1,
                listData:[]
            }
        },
        methods:{
            loadTop(){
                console.log("下拉刷新")
                this.$http.get("/api/recommend/home?page=1&num=20").then(res=>{
                    this.listData =res.data.data.list
                    this.page = 1;
                    //加载完最新的数据，  结束下拉刷新的状态
                    this.$refs.loadmore.onTopLoaded();
                })
                // setTimeout(()=>{
                //     //一秒后结束下拉刷新的状态
                //     this.$refs.loadmore.onTopLoaded();
                // },1000)
            },
            loadBottom(){
                this.page++
                  this.$http.get("/api/recommend/home?page="+this.page+"&num=20").then(res=>{
                    this.listData = this.listData.concat(res.data.data.list)
                    //加载完最新的数据，  结束加载更多的状态
                    this.$refs.loadmore.onBottomLoaded();
                })
                 console.log("上拉加载")
            }
        },
        mounted(){
            this.$http.get("/api/recommend/home?page=1&num=20").then(res=>{
                this.listData =res.data.data.list
            })
        }
    }
</script>



<style scoped >

.list { background:#eee }
.list li  { padding:10px; margin:10px 0; background:#fff; position:relative}
.list img { width:100px; position:absolute; left:10px; top:10px;}
.item-info {  height:130px; padding-left:110px; }
.price { color:#f93}

</style>



