// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import vueResource from "vue-resource" //导入vueResource
import axios from "axios" //导入axios
import store from './store'
import './iconfont/iconfont.css'
// import MintUI from 'mint-ui'   可能会有很多用不到的组件也会加载进来
// import 'mint-ui/lib/style.css'

 //按需导入第三方组件
import {Button, Swipe, SwipeItem ,Actionsheet,IndexList, IndexSection, Cell,InfiniteScroll,Toast} from 'mint-ui'
import { Loadmore,Lazyload } from 'mint-ui';





//导入自己的组件
import Header from './components/Header.vue'
import Content from './components/Content.vue'
import Footer from './components/Footer.vue'

//注册第三方组件
Vue.component(Button.name,Button)
Vue.component(Swipe.name, Swipe);
Vue.component(SwipeItem.name, SwipeItem);
//Actionsheet 底部弹窗
Vue.component(Actionsheet.name, Actionsheet);
//IndexList (选择城市列表)
Vue.component(IndexList.name, IndexList);
Vue.component(IndexSection.name, IndexSection);
Vue.component(Cell.name, Cell);
//下拉加载（刷新）
Vue.component(Loadmore.name, Loadmore);

//注册自己的组件
Vue.component(Header.name,Header)
Vue.component("v-content",Content)
Vue.component("v-footer",Footer)

Vue.use(InfiniteScroll); //使用无线加载组件
Vue.use(vueResource) //使用vueResource插件
Vue.use(Lazyload);
//可以在Vue的原型上扩展方法 (在vue的组件里面可以直接通this调用)
Vue.prototype.axios = axios
Vue.prototype.Toast = Toast  //添加原型属性
Vue.prototype.alert = function(str){
  alert(str)
}  //添加原型属性


Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  data:{},
  el: '#app',
  router,
  store,
  template: '<App/>',
  components: { App }
})



